.coming-soon-background {
  background-repeat: no-repeat;
  height: 100%;
  min-height: 100vh;

  .st-coming {
    padding: 0 1em;
    text-align: center;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .st-coming-inner {
      padding: 2em 1em;
      background: rgba(var(--bs-dark-rgb), 0.5);
      max-width: 50em;
      margin: 0 auto;
      -webkit-backdrop-filter: blur(1.5625em);
      backdrop-filter: blur(1.5625em);
      position: relative;
      display: block;
      align-self: center;
    }
  }

  .iq-maintenance-message {
    .iq-maintenance-title {
      margin-top: 0;
      margin-bottom: 0.25em;
      font-size: 3.209em;
      line-height: 1.3;
    }

    .iq-maintenance-desc {
      margin: 0;
      font-size: 1.125em;
    }
  }

  .countdown {
    list-style: none;
    margin: 1.25em 0 0 0;
    padding: 0;
    text-align: center;
    display: inline-flex;

    li {
      position: relative;
      display: inline-block;
      margin: 0 1.25em;
      width: 8.75em;
      padding: 0.875em;

      &::after {
        content: ":";
        position: absolute;
        right: -0.6em;
        top: 50%;
        transform: translateY(-50%);
        font-size: 3.125em;
        color: var(--bs-white);
        line-height: 3.125em;
      }

      span {
        font-size: 3.125em;
        font-weight: 800;
        line-height: 1.3;
        color: var(--bs-white);
      }

      .days_text,
      .minutes_text,
      .hours_text,
      .seconds_text {
        margin: 0.5em 0 0;
      }

      &:last-child {
        &:after {
          content: none;
        }
      }
    }
  }
}

@media (max-width: 992px) {
  .coming-soon-background {
    .st-coming {
      .st-coming-inner {
        max-width: 80%;
      }
    }

    .countdown {
      li {
        width: 5.625em;
        margin: 0 1em;

        span {
          font-size: 1.5em;
        }
      }
    }

    .iq-maintenance-message {
      .iq-maintenance-title {
        font-size: 2em;
      }
    }
  }
}

@media (max-width: 767px) {
  .coming-soon-background {
    .iq-maintenance-message {
      padding: 2em;
      width: 95%;
    }

    .countdown {
      li {
        width: 3.375em;
        margin: 0 0.625em;

        &::after {
            right: -.35em;
        }
      }
    }
  }
}

@media (max-width: 479px) {
  .coming-soon-background {
    .countdown {
      flex-direction: column;
      margin: 1em 0 0 0;

      li {
        width: auto;
        margin: 0 0.625em 0.625em;

        &::after {
          right: 45%;
          top: auto;
          bottom: -0.75em;
          transform: translateY(0) translateX(55%) rotate(90deg);
          line-height: inherit;
        }
      }
    }

    .st-coming {
      .st-coming-inner {
        padding: 1em;
      }

      .iq-maintenance-message {
        padding: 0;
        width: 100%;
      }
    }
  }
}