.iq-card-geners {
  .block-images {
    backface-visibility: hidden;

    .img-box {
      background: linear-gradient(0deg, rgba(12, 11, 17, 0.25) 0%, rgba(12, 11, 17, 0.25) 100%), linear-gradient(180deg, rgba(12, 11, 17, 0.00) 56%, rgba(12, 11, 17, 0.20) 100%);

      &::before {
        position: absolute;
        content: "";
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        border-bottom: .25em solid var(--bs-primary);
        background: rgba(var(--bs-black), 0.8);
        width: 100%;
        height: 100%;
        opacity: 0;
        z-index: 9;
        transition: all 0.5s ease-in-out;
        border-radius: var(--bs-border-radius-lg);
      }

      img {
        mix-blend-mode: overlay;
      }
    }

    &:hover {
      .img-box {
        &::before {
          opacity: 1;
        }
      }
    }
  }

  .img-box {
    border-radius: var(--bs-border-radius-lg);

    img {
      height: 100%;
      width: 100%;
      aspect-ratio: 5/3;
      object-position: top;
      object-fit: cover;
      border-radius: var(--bs-border-radius-lg);
    }

    &::before {
      content: "";
      background: linear-gradient(90deg, rgba(var(--bs-dark-rgb), .8), rgba(var(--bs-dark-rgb), .4) 50%, rgba(var(--bs-dark-rgb), 0));
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
    }
  }

  .blog-description {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 10;
    margin: auto;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: capitalize;

    .iq-title {
      a {
        font-size: 1em;
        color: var(--bs-white);
      }
    }
  }

  &:hover {
    z-index: 99;
    transform: scale3d(1.1, 1.1, 1) translateZ(0) perspective(31.25em);
    transform-origin: 50% 50%;
    transition: all .6s ease 0s;

    &::before {
      opacity: 1;
    }
  }

  &::before {
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    border-bottom: 0.25em solid var(--bs-primary);
    background: rgba(var(--bs-dark-rgb), .8);
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 9;
    transition: all .5s ease-in-out;
    border-radius: var(--bs-border-radius-lg);
  }
}

// geners style grid
.geners-style-grid {
  .slide-items {
    margin-bottom: 1.875em;
  }
}

@media (max-width:767px) {
  .geners-card {
    li.swiper-slide {
      padding: 0 .4687em;
    }
  }
}